*
{
    margin: 0;
    padding: 0;
    font-family: 'Signika', sans-serif;
    color: #FFF;
}
.main{
    width: 100%;
    height: fit-content;
    display: flex;  
    justify-content: center;
    background-color: black;
    
}
.nav
{
    width: 25%;
    height: 900px;
    font-size: 25px;
    border-right: solid 0.1px #353535;
    display: flex;
    flex-direction: column;
    align-items: center; 
    gap: 40px;
}
.logo
{
    width: 50px;
    height: 70px;
    display: flex;
    justify-content: start;
    align-items: center;
    margin-right: 120px;
    font-family: 'Crimson Text', serif;
}
.list
{
    width: 45%;
    display: flex;
    gap: 20px;
    flex-direction: column;
    align-items: start;
}
