.list a 
{
    text-decoration: none;
    color: #FFF;
    display: flex;
    gap: 20px;
}
.wee button
{
    width: 150px;
    height: fit-content;
    padding: 10px;
    font-size: 20px;
    color: #FFF;
    border: none;
    border-radius: 30px;
    background-color: #84dc66;
    transition: 0.4s;
}
.wee button:hover
{
    background-color: #37881c;
}
.out
{
    width: 40%;
    height: fit-content;
}
.trending
{
    width: 25%;
    height: 900px;
    border-left: solid 0.1px #353535;
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;
    justify-content: center;
}
.trending .search
{
    width: 80%;
    height: 40px;
    background-color: #353535;
    border-radius: 40px;
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
}
.trending .search i
{
    background-color: #353535;
    color: white;
}
.trending .search input
{
    width: 80%;
    background-color: #353535;
    outline: none;
    border: none;
    color: white;
}
.verified
{
    width: 80%;
    height: 130px;
    background-color: #353535;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    padding-left: 10px;
    gap: 7px;
}
.verified button
{
    width: fit-content;
    background-color: #008E86;
    border: none;
    outline: none;
    padding: 5px;
    border-radius: 20px;
}
.verified a
{
    text-decoration: none;
}
.trend
{
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center; 
}
.whatsup
{
    width: 78%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap: 30px;
    background-color: #353535;
    border-radius: 20px;
    padding: 10px;
    text-align: start;
}
.whatsup p
{
    color: #999999;
}
.trend2 p
{
    display: flex;
}
.type1
{
    display: flex;
    justify-content: space-between;
}
.type2
{
    display: flex;
    justify-content: space-between;
}
.type3
{
    display: flex;
    justify-content: space-between;
}
.type4
{
    display: flex;
    justify-content: space-between;
}
.type5
{
    display: flex;
    justify-content: space-between;
}
.type6
{
    display: flex;
    justify-content: space-between;
}
.acc
{
    width: 80%;
    height: 70px;
    display: flex;
}
.acc .pic
{
    width: 100px;
    height: 100px;
    height: fit-content;
    outline: none;
    border-radius: 100px;
    background-size: cover;
    background-repeat: no-repeat;
}
