.home{
    display: flex;
    flex-direction: column;
    border-left: 1px solid #353535;
    border-right: 1px solid #353535;
}
.home h1{
    width: 130px;
    height: 70px;
    text-align: start;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
}
.whats{
    width: 100%;
    height: 150px;
    border-top: solid 1px #353535;
    border-bottom: solid 1px #353535;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}
.whats .photo img{
    width: 50px;
    height: 50px;
    border: 0.1px solid black;
    border-radius: 100px;
    margin-bottom: 60px;
}
.post{
    display: flex;
    flex-direction: column;
    align-items: end;
}
.post input{
    width: 450px;
    height: 100px;
    border: none;
    background: none;
    outline: none;
    font-size: 20px;
}
.post button{
    width: 70px;
    height: 30px;
    border: none;
    border-radius: 20px;
    background-color: #84dc66;
    transition: 0.4s;
}
.post button:hover{
    background-color: #37881c;
}
.post input::placeholder{
    font-size: 20px;
}
.get img{
    width: 50px;
    height: 50px;
    border: 0.1px solid black;
    border-radius: 100px;
    padding: 10px;
}
.get{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column-reverse;
}
.shown{
    width: 100%;
    height: 160px;
    border-top: 0.1px solid #353535;
    border-bottom: 0.1px solid #353535;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
.user{
    display: flex;
    gap: 0px;
}
.user h3
{
    padding-top: 25px;
}
.delete{
    display: flex;
    justify-content: end;
    align-items: center;
    gap: 120px;
}
.delete button{
    width: 70px;
    height: 30px;
    background-color: #84dc66;
    border-radius: 20px;
    border: none;
    transition: 0.4s;
    margin-right: 30px;
}
.delete button:hover{
    background-color: #37881c;
}
.shown p
{
    font-size: 23px;
}
.kholi{
    display: flex;
    flex-direction: column;
    gap: 20px;
    border-bottom: 1px solid #353535;
    padding-bottom: 15px;
}
.identity{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    gap: 30px;
    margin-left: 20px;
}
.identity img
{
    width: 70px;
    height: 70px;
    border-radius: 35px;
}
.identity h3{
    width: fit-content;
    height: fit-content;
    display: flex;
}
.identity p{
    display: flex;
    align-items: center;
    padding-left: 5px;
}
.identity p{
    font-size: 12px;
    font-weight: lighter;
}
.kholi .post1 img{
    border-radius: 20px;
}
.kholi .content
{
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: start;
}
.icons1{
    display: flex;
    justify-content: space-around;
}